    <menuView/>
</template>

<script>
import dataManager from '../../../dataManager';
import menuView from '@/views/menu/no-tab/menuView.vue'
const convertStyle = () => {
	document.body.style.setProperty('--app-height-val', `${window.innerHeight}`);
	document.body.style.setProperty('--app-height', `${window.innerHeight}px`);
	document.body.style.setProperty('--app-width-val', `${window.innerWidth}`);
}


export default {
    data() {
		return {
			categories: []
		}
	},
	computed: {
		orderCount() {
			return this.$store.getters.orderItems.length;
		}
	},
	components: {
		// orderSummary,
        menuView
	},
	mounted() {
		dataManager.getAllCategories()
			.then(c => this.categories = c);
		window.addEventListener("resize", convertStyle);
		window.addEventListener("DOMContentLoaded", convertStyle);
		convertStyle();
	}
}
</script>

