import Vue from 'vue';
import Vuex from 'vuex';
import dataManager from '../dataManager';
// import mockItem from '../../mock-data/items.json';
import dataGetter from '../dataManager';
Vue.use(Vuex);
let _order = window.localStorage.getItem('order');
let _transactionId = window.localStorage.getItem('transactionId');
/**
 * Status: Selected, Ordered, Served
 */
export default new Vuex.Store({
	state: {
		order: _order ? JSON.parse(_order) : { itemLines: [], infocodes: [] },
		transactionId: _transactionId ? JSON.parse(_transactionId) : '',
		tableSession: {},
		unsentOrder: {},
		kitchenOrders: [
			/* {
				id
				tableNo
				sessionId
				items: [
					{
						item_no
						qty
					}
				]
				createdTime
				servedTime
				status
			} */
		],
		tableOrder: {
			tableNo: '',
			sessionId: '',
			itemLines: [],
			infocodes: [],
		}
	},
	mutations: {
		SET_order (state, order) {
			state.order = order;
		},
		SAVE (state) {
			window.localStorage.setItem('order', JSON.stringify(state.order));
			windoww.localStorage.setItem('transactionId', state.transactionId);
		},
		SET_TS_sessionId (state, sessionId) {
			state.tableSession.sessionId = sessionId;
		},
		SET_TS_tableNo (state, tableNo) {
			state.tableSession.tableNo = tableNo;
		},
		SET_TS_entryTime(state, entryTime) {
			state.tableSession.entryTime = entryTime;
		},
		SET_tableSession(state, tableSession) {
			state.tableSession = tableSession;
		},
		SET_kitchenOrder(state, kots) {
			state.kitchenOrders = kots;
		},
		SET_tableOrder(state, tableorder) {
			state.tableOrder = tableorder;
		},
		SET_transactionId(state, transactionId) {
			state.transactionId = transactionId;
		}
	},
	actions: {
		async addQtyWithLineNo({commit, state}, lineNo) {
			let order = state.order;
			let existingLine = order.itemLines.find(x => x.line_no === lineNo);
			if (existingLine) {
				existingLine.quantity += 1;
			}
			commit('SET_order', order);
			return;
		},
		async removeQtyWithLineNo({commit, state}, lineNo) {
			let order = state.order;
			let existingLine = order.itemLines.find(x => x.line_no === lineNo);
			if (existingLine && existingLine.quantity > 1) {
				existingLine.quantity -= 1;
			}
			commit('SET_order', order);
			return;
		},
		async addToOrderWithDetail({commit, state}, itemDetail) {
			let order = state.order;
			// find if line exist
			let _lineNo = getItemLineNoWithInfocodes(state, itemDetail);
			let existingLine = order.itemLines.find(x => x.line_no === _lineNo);
			if (existingLine) {
				existingLine.quantity += itemDetail.qty;
			} else {
				// add new line
				let _item = await dataManager.findItemsByNo(itemDetail.item_no);
				let _itemLineNo = (state.order.itemLines.length + 1) * 10000;
				let _unitPrice = itemDetail.uom_unit_price ? itemDetail.uom_unit_price : _item.unit_price;
				let _variantId = itemDetail.item_no+itemDetail.uom_id;
				let itemLine = {
					line_no: _itemLineNo,
					item_no: itemDetail.item_no,
					quantity: itemDetail.qty,
					unit_price: _unitPrice,
					description: _item.description,
					uom_id: itemDetail.uom_id,
					variant_id: _variantId
				}
				if (itemDetail.hasOwnProperty('infocodeDetails')) {
					for (const infocodeDetail of itemDetail.infocodeDetails) {
						let infocodeline = {
							line_no: (state.order.infocodes.length + 1) * 10000,
							item_line_no: _itemLineNo,
							infocode_code: infocodeDetail.infocode.code,
							subcode_code: infocodeDetail.subcode.line_code,
							infocode_object: infocodeDetail.infocode,
							subcode_object: infocodeDetail.subcode,
							variant_id: _variantId
						}
						order.infocodes.push(infocodeline);
					}
				}
				order.itemLines.push(itemLine);
			}
			commit('SET_order', order);
			return;
		},
		async addToOrder({commit, state}, itemNo) {
			let order = state.order;
			let existingLine = order.itemLines.find(
				line => line.item_no === itemNo
			);
			if (existingLine) {
				existingLine.quantity += 1;
			} else {
				let _item = await dataGetter.findItemsByNo(itemNo);
				let itemLine = {
					line_no: (state.order.itemLines.length + 1)* 10000,
					item_no: itemNo,
					quantity: 1,
					unit_price: _item.unit_price,
					description: _item.description
				};
				order.itemLines.push(itemLine);
			}
			commit('SET_order', order);
			return;
		},
		async removeFromOrder({commit, state}, itemNo) {
			let order = state.order;
			let existingLine = order.itemLines.find(
				line => line.item_no === itemNo
			);
			if (existingLine && existingLine.quantity > 1) {
				existingLine.quantity -= 1;
			}
			if (existingLine.quantity <= 0) {
				let index = order.itemLines.indexOf(existingLine);
				order.itemLines.splice(index, 1);
			}
			commit('SET_order', order);
			return;
		},
		async removeLine({commit, state}, orderItem) {
			let order = state.order;
			let itemIdx = order.itemLines.indexOf(orderItem);
			order.itemLines.splice(itemIdx, 1);
			let infocodes = order.infocodes.filter(x => x.item_line_no === orderItem.line_no);
			if (infocodes.length > 0) {
				for (const infocode of infocodes) {
					let infocodeIdx = order.infocodes.indexOf(infocode);
					order.infocodes.splice(infocodeIdx, 1);
				}
			}
			commit('SET_order', order);
			return
		},
		async resetOrder({commit}) {
			commit('SET_order', { itemLines: [], infocodes: [] });
		},
		async setTableSession({commit}, tableSession) {
			commit('SET_tableSession', tableSession);
			return;
		},
		async addKitchenOrder({commit, state}) {
			let kos = state.kitchenOrders;
			kos.push({
				tableNo: state.tableSession.tableNo,
				sessionId: state.tableSession.sessionId,
				items: state.order.itemLines,
				infocodes: state.order.infocodes,
				// orderNo: state.order.no
			});
			this.dispatch('resetOrder');
			commit('SET_kitchenOrder', kos);
			return;
		},
		async addTableOrder({commit, state}) {
			let tabOrd = state.tableOrder;
			if (!tabOrd) {
				tabOrd = {
					tableNo: state.tableSession.tableNo,
					sessionId: state.tableSession.sessionId,
				}
			}
			// add itemLines
			if (state.order.itemLines.length > 0) {
				let itemLineNo = (state.tableOrder.itemLines.length + 1) * 10000;
				state.order.itemLines.map(itemLine => {
					let newtableOrderItemLine = JSON.parse(JSON.stringify(itemLine));
					newtableOrderItemLine.line_no = itemLineNo;
					tabOrd.itemLines.push(newtableOrderItemLine);
					
					// add related infocodes
					let relatedInfocodes = state.order.infocodes.filter(x => x.item_line_no == itemLine.line_no)
					if (relatedInfocodes.length > 0) {
						let infocodeLineNo = (state.tableOrder.infocodes.length + 1) * 10000;
						relatedInfocodes.map(infocode => {
							let newtableOrderInfoCodeLine = JSON.parse(JSON.stringify(infocode));
							newtableOrderInfoCodeLine.line_no = infocodeLineNo;
							newtableOrderInfoCodeLine.item_line_no = itemLineNo;
							tabOrd.infocodes.push(newtableOrderInfoCodeLine);
							infocodeLineNo += 10000;
						});
					}
					itemLineNo += 10000;
					
				});
			}
			this.dispatch('resetOrder');
			commit('SET_tableOrder', tabOrd);
			return;
		},
		async setTransactonId({commit}, transactionId) {
			commit('SET_transactionId', transactionId);
			return;
		}
	},
	getters: {
		orderItems: (state) => state.order.itemLines,
		infocodes: (state) => state.order.infocodes,
		tableNo: (state) => state.tableSession.tableNo,
		sessionId: (state) => state.tableSession.sessionId,
		entryTime: (state) => state.tableSession.entryTime,
		kitchenOrders: (state) => state.kitchenOrders,
		tableOrderItems: (state) => state.tableOrder.itemLines,
		tableOrderInfocodes: (state) => state.tableOrder.infocodes
		// orderNo: (state) => state.order.no
	},
	modules: {}
});

const getItemLineNoWithInfocodes = (state, itemDetail) => {
	let _variant_id = itemDetail.item_no+itemDetail.uom_id;
	let _itemLines = state.order.itemLines.filter(x => x.variant_id === _variant_id);
	if (_itemLines.length > 0) {
		for (const itemLine of _itemLines) {
			let _lineNo = itemLine.line_no;
			let sc = state.order.infocodes.filter(x => x.item_line_no === _lineNo);
			// Handle no infocodes
			if (!itemDetail.hasOwnProperty('infocodeDetails') && sc.length === 0) {
				return _lineNo;
			} else {
				// Handle with infocodes
				let matchLeng = itemDetail.infocodeDetails.length === sc.length;
				let matchSubcode = itemDetail.infocodeDetails.every((ele, idx) => {
					console.log(sc);	
					return ele.subcode.line_code === sc[idx].subcode_code;
					});
				let matchResult = matchLeng && matchSubcode;
				if (matchResult) {
					return _lineNo;
				}
			}
		}
	}
	return 0;
}