<template>
	<div id="order-cell">
		<b-row>
			<b-col cols="12" style="text-align: left; margin: 5px" 
				v-html="orderItem.description +uomTxt+ infocodeText"
			></b-col>
		</b-row>
		<b-form-row class="align-mid" id="content">
				<div id="buttonWrapper" class="align-mid">
					<div 
						v-if="operations"
						@click="removeLine(orderItem)" 
						class="button"
					>
						<b-icon icon="x"></b-icon>
					</div>
				</div>
				<div class="left">
					{{ combinedUnitPrice }} x
				</div>
				<qtyControl
					:operation="operations"
					:qty="orderItem.quantity"
					@add="addQtyWithLineNo(orderItem.line_no)"
					@minus="removeQtyWithLineNo(orderItem.line_no)"
				></qtyControl>
				<div
					class="right"
				>
					${{ (orderItem.quantity * combinedUnitPrice).toFixed(2) }}
				</div>
		</b-form-row>
	</div>
</template>

<script>
import qtyControl from '@/components/sub-components/qtyControl';
import dataManager from '../dataManager';

export default {
	components: {
		qtyControl
	},
	props: {
		orderItem: {
			type: Object,
			default() {
				return {};
			}
		},
		infocodes: {
			type: Array,
			default() {
				return [];
			}
		},
		operations: {
			type: Boolean,
			default() {
				return false;
			}
		}
	},
	data() {
		return {
			uomTxt: '',
		}
	},
	computed: {
		infocodeText() {
			let text = '';
			if (this.orderItem) {
				let infocodes = this.infocodes.filter(x => x.item_line_no === this.orderItem.line_no);
				infocodes.map((infocode) => {
					text += `<br>     -- ${infocode.infocode_object.description}: ${infocode.subcode_object.description}`;
				});	
			}
			return text;
		},
		combinedUnitPrice() {
			let p = 0;
			if (this.orderItem) {
				p = this.orderItem.unit_price;
				let infocodes = this.infocodes.filter(x => x.item_line_no === this.orderItem.line_no);
				infocodes.map((infocode) => {
					if (infocode.subcode_object.amount) {
						p += infocode.subcode_object.amount
					}
				});	
			}
			return p;
		}
	},
	methods: {
		async getUOMText() {
			let text = '';
			if (this.orderItem) {
				let uom = await dataManager.getUOMById(this.orderItem.uom_id);
				text = uom? `(${uom.description})`: '';
			}
			this.uomTxt = text;
		},
		addQtyWithLineNo(lineNo) {
			this.$store.dispatch('addQtyWithLineNo', lineNo);
		},
		removeQtyWithLineNo(lineNo) {
			this.$store.dispatch('removeQtyWithLineNo', lineNo);
		},
		removeLine(orderItem) {
			this.$store.dispatch('removeLine', orderItem);
		},
	},
	async mounted() {
		await this.getUOMText();
	}
};
</script>

