<template>
    <div id="uomList">
       <h6>大小</h6>
        <div id="uomList-body">
            <div id="button-wrapper" v-for="(uom, idx) in uomObj" :key="idx">
                <div class="align-mid">
                    <b-button id="button" :pressed.sync="uom.state"  @click="handleStateChange(idx)">
                        <div v-if="uom.uomDetail.icon">
                            <img  :src="`${uom.uomDetail.icon}`" class="img">
                        </div>
                        <div v-else class="align-mid img">
                            <div>{{uom.uomDetail.description}}</div>
                        </div>
                    </b-button>
                </div>
               <p v-if="uom.uomDetail.icon" class="align-mid">{{uom.uomDetail.description}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import dataManager from '../../dataManager';
export default {
    props: {
        itemUOMs: {
            type: Array,
            default() {
                return [];
            }
        }
    },
    data() {
        return {
            uomObj: []
        }  
    },
    methods: {
        amountStr(subcode) {
            let str = '';
            if (subcode.amount && subcode.amount > 0) {
                str += '$' + subcode.amount;
            }
            return str;
        },
        handleStateChange(i) {
            this.uomObj.forEach((b, index) => b.state = i === index);
            this.$emit('uomChange', this.uomObj);
        }
    },
    components: {
    },
    async mounted() {
        let idx = 0;
        for (const itemUom of this.itemUOMs) {
            let uom = await dataManager.getUOMById(itemUom.uom_id);
            let uomDetail = {
                id: uom.id,
                unit_price: itemUom.unit_price,
                description: uom.description,
                icon: uom.icon
            }
            let uomState = { uomDetail: uomDetail, state: false }
            if (idx==0) {
                uomState.state = true;
            }
            this.uomObj.push(uomState);
            idx ++;
        }
        this.$emit('uomChange', this.uomObj);
    }
}
</script>

