<template>
    <div id="itemDetail">
        <div id="scrollable">
            <div id="top">
                <img :src="`${item.picture}`" class="image" />
            </div>
            <div id="middle1">
                <p 
                    class="text"
                    v-html="item.long_description"
                >
                </p>
                <infocodePanel
                    :item_no="item.item_no"
                    @subcodeChange="subcodeChange"
                    @uomChange="uomChange"
                />
            </div>
        </div>
        <div id="footer">
            <div id="footer-top">
                <div class="align-mid">
                    <h6>數量</h6>
                    <!-- <p>數量</p> -->
                </div>
                
                <div id="qty-ctrl">
                    <qtyControl
                        :qty="qty"
                        @add="addQty"
                        @minus="minusQty"
                    ></qtyControl>
                </div>
            </div>
            <div id="footer-bottom">
                <b-button
                    squared
                    block
                    @click="handleAdd"
                >
                    <div> 加入 ${{price}}</div>
                </b-button>
            </div>
        </div>
        <b-modal id="removePrompt" ref="removePrompt"
            centered
        >
        </b-modal>
    </div>
</template>

<script>
import qtyControl from '@/components/sub-components/qtyControlLarge';
import infocodePanel from '@/components/info-codes/infocodePanel';
export default {
    props: {
        item: {
			type: Object,
			default() {
				return {};
			}
		}
    },
    data() {
        return {
            infocodeDetails: [],
            qty: 1,
            uomDetail: {},
        }
    },
    computed: {
        price() {
            let _basePrice = this.item.unit_price;
            if (this.uomDetail.unit_price > 0){
                _basePrice = this.uomDetail.unit_price
            } 
            let _infoCodePrice = 0;
            this.infocodeDetails.map(x => {
                if (x.subcode.amount) {
                    _infoCodePrice += x.subcode.amount;
                }
            });
            return (this.qty * (_basePrice + _infoCodePrice)).toFixed(2);
        }
    },
    components: {
		qtyControl,
        infocodePanel
	},
    methods: {
        uomChange(uom) {
            let selectedUOM = uom.filter(u => u.state === true)[0];
            this.uomDetail = selectedUOM.uomDetail;
        },
        subcodeChange(sc) {
            let exist = false;
            sc.subcodeStates.map(subcodeState => {
                // Find if subcode already exist
                this.infocodeDetails.map(infocodeDetail => {
                    exist = false;
                    if (infocodeDetail.infocode.code === subcodeState.subcode.code 
                        && infocodeDetail.subcode.line_code === subcodeState.subcode.line_code) {
                        exist = true;
                    }

                    // Remove existing where state = false && exist
                    if (exist) {
                        // remove if false
                        let index = this.infocodeDetails.indexOf(infocodeDetail);
                        this.infocodeDetails.splice(index, 1);
                    } 
                });
                // Add new where state = true && not existing
                if (subcodeState.state) {
                    let newInfocodeDetail = {
                        infocode: sc.infocode,
                        subcode: subcodeState.subcode
                    }
                    this.infocodeDetails.push(newInfocodeDetail);
                }
            });
        },
        handleAdd() {
            let itemDetail  = {
                qty: this.qty,
                item_no: this.item.item_no,
                uom_id: this.uomDetail.id,
                uom_unit_price: this.uomDetail.unit_price
            }
            if (this.infocodeDetails.length > 0) {
                itemDetail.infocodeDetails = this.infocodeDetails
            }
            this.$store.dispatch('addToOrderWithDetail', itemDetail);
            this.$emit('close');
        },
        addQty() {
            this.qty ++;
        },
        minusQty() {
            if (this.qty > 1) {
                this.qty --;
            }
        }
	}
}
</script>

