<template>
    <div id="infocodeList">
       <h6>{{infoCodeObj.infocode.description}} 可選({{infoCodeObj.infocode.maxSelect}})</h6>
        <div id="infocode-body">
            <div id="button-wrapper" v-for="(cs, idx) in infoCodeObj.subcodeStates" :key="idx">
                <div class="align-mid">
                    <b-button id="button"  :pressed.sync="cs.state" @click="handleStateChange(idx)">
                        <div v-if="cs.subcode.icon">
                            <img :src="`${cs.subcode.icon}`" class="img">
                        </div>
                        <div v-else class="align-mid img">
                            <div>{{cs.subcode.description}} </div>
                            <div>{{amountStr(cs.subcode)}}</div>
                        </div>
                    </b-button>
                </div>
                <p v-if="cs.subcode.icon" class="align-mid">{{cs.subcode.description}}</p>
            </div>
        </div>
    </div>
</template>

<script>
import dataManager from '../../dataManager';
export default {
    props: {
        itemInfoCode: {
            type: Object,
            default() {
                return {};
            }
        },
    },
    data() {
        return {
            subcodes: [],
            infoCodeObj: {
                infocode: {},
                subcodeStates: []
            },    
        }  
    },
    methods: {
        amountStr(subcode) {
            let str = '';
            if (subcode.amount && subcode.amount > 0) {
                str += '$' + subcode.amount;
            }
            return str;
        },
        handleStateChange(i) {
            // handle required 1
            if (this.infoCodeObj.infocode.minSelect === 1) {
                this.infoCodeObj.subcodeStates.forEach((b, index) => b.state = i === index);
            } else if (this.infoCodeObj.infocode.minSelect === 0 && this.infoCodeObj.infocode.maxSelect === 1) {
                // handle not required and can only choose 1
                if (this.infoCodeObj.subcodeStates[i].state === true) {
                    this.infoCodeObj.subcodeStates.forEach((b, index) => b.state = i === index);
                }
                // do nothing for deselect
            } else {
                let selected = 0;
                this.infoCodeObj.subcodeStates.map((s) => {
                    if (s.state) {
                        selected ++;
                    }
                })
                if (this.infoCodeObj.infocode.maxSelect < selected) {
                    this.infoCodeObj.subcodeStates[i].state = false;
                }
            }
            this.$emit('subcodeChange', this.infoCodeObj);
        }
    },
    components: {
    },
    async mounted() {
        let infoCode =  await dataManager.getInfoCode(this.itemInfoCode.infocode_code);
        this.infoCodeObj.infocode = infoCode;
        let subcodes = await dataManager.getInfoSubcodes(this.itemInfoCode.infocode_code);
        subcodes.map((sc,idx) => {
            let subcodeState = { subcode: sc, state: false}
            if (idx ==0 && idx < infoCode.minSelect) {
                subcodeState.state = true;
            }
            this.infoCodeObj.subcodeStates.push(subcodeState);
        });
        this.$emit('subcodeChange', this.infoCodeObj);
    }
}
</script>

